import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    view: '10',
    clinics: [],
    feedbacks: [],
    searchedClinics: [],
    aboutSpecialist: [],
    burgerActiveForNav: false,
    searchQuery: '',
    clinicsTitle: ''
  },
  getters: {
    view(state) {
      return state.view;
    },
    clinics(state) {
      return state.clinics;
    },
    feedbacks(state) {
      return state.feedbacks
    },
    aboutSpecialist(state) {
      return state.aboutSpecialist
    },
    searchedClinics(state) {
      return state.searchedClinics
    },
    searchQuery(state) {
      return state.searchQuery
    },
    burgerActiveForNav(state) {
      return state.burgerActiveForNav
    },
    clinicsTitle(state) {
      return state.clinicsTitle
    }
  },
  mutations: {
    burgerMenuActive(state) {
      state.burgerActiveForNav = !state.burgerActiveForNav
    },
    getSpecialist(state, payload) {
      axios.get(`http://18.206.211.243/specialists/${payload}`)
          .then(res => {
            state.aboutSpecialist = res.data
          })
    },
    viewChange(state, payload) {
      state.view = payload
    },
    getClinics(state, payload) {
      axios.get(`http://18.206.211.243/clinics/${payload}`)
          .then(res => {
            state.clinics = res.data
          })
    },
    getBySearch(state, payload) {
      state.searchQuery = payload
      axios.get(`http://18.206.211.243/clinics/?search=${payload}`)
          .then(res => {
            state.searchedClinics = res.data.results
          })
    },
    getFeedbacks(state, payload) {
      axios
          .get(`http://18.206.211.243/feedbacks/`)
          .then(res => {
            for (let i = 0; i < res.data.results.length; i++) {
              if (res.data.results[i].specialist == payload) {
                state.feedbacks.push(res.data.results[i])
              }
            }
          })
    }
  },
  actions: {
  },
  modules: {
  }
})
