<template>
  <div class="home" :class="{'home-active': burgerActiveForNav}">
    <vs-card v-for="(item, index) in (searchQuery) !== '' ? searchedClinics : clinics" :type="view">

      <template #title>
        <h3 style="text-transform: capitalize">{{ item.title }}</h3>
      </template>
      <template #img>
        <img :src="item.image" alt="photo">
      </template>
      <template #text>
        <p style="display:flex; align-items: center; gap: 10px">
          <img width="25" height="25" src="../assets/logo/clock.png" alt="clock">
          <span>{{ item.open_time }}/{{ item.close_time }}, {{ item.start_day }}-{{ item.end_day }}</span>
        </p>
        <p style="display:flex; align-items: center; gap: 10px">
          <img width="25" height="25" src="../assets/logo/location.png" alt="location">
          <span>{{ item.district }} {{ item.street }} {{ item.home_num }}</span>
        </p>
        <vs-button
            style="width: 150px; margin-left: auto"
            color="#005C5F"
            :active="active === index"
            @click="routerChange(index, item.id)"
        >
          Перейти
        </vs-button>
      </template>
    </vs-card>
    <div class="pagination" v-if="searchQuery === ''">
      <vs-pagination @input="changePagination" color="#005C5F" v-model="page" :length="+count" />
    </div>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import axios from 'axios'

export default {
  name: 'HomeView',
  data() {
    return {
      page: 1,
      active: '',
      count: '',
      clinics: [],
    }
  },
  computed: {
    ...mapGetters(['view', 'searchedClinics', 'searchQuery', 'burgerActiveForNav'])
  },
  methods: {
    changePagination() {
      axios.get(`http://18.206.211.243/clinics/?search=${this.searchQuery}&limit=10&offset=${(this.page * 10) - 10}`)
          .then(res => {
            this.clinics = res.data.results
            this.count = Math.ceil(res.data.count / 10)
          })
    },
    routerChange(index, id) {
      this.active = index
      this.$router.push(`/about-clinic/${id}`)
    }
  },
  mounted() {
    this.changePagination()
  }
}
</script>

<style>


.pagination {
  position: fixed;
  width: 100%;
  left: 0;
  bottom: 0;
  background: white;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 20px 50px 20px 0;
  z-index: 3;
}

.home {
  padding: 95px 50px 90px 270px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  transition: .5s ;
}
</style>