<template>
  <div class="navbar " :class="{'navbar-active': burgerActiveForNav}">
    <div class="menu">
      <vs-button
          class="burger_menu"
          style="border-radius: 5px"
          upload
          icon
          color="#005C5F"
          @click="burgerActive"
      >
        <i v-if="burgerActiveForNav" class="el-icon-s-fold"></i>
        <i v-else class="el-icon-s-unfold"></i>
      </vs-button>
      <vs-input @input="getBySearchQuery" v-model="search" placeholder="Поиск">
        <template #icon>
          <img width="10" height="10" src="../assets/logo/search.png" alt="search">
        </template>
      </vs-input>
    </div>
    <div class="navbar-filters">
      <vs-select
          @change="changeView"
          v-model="view"
          placeholder="Вид"
      >
        <vs-option label="Карты" value="10">
          Карты
        </vs-option>
        <vs-option label="Строки" value="3">
          Строки
        </vs-option>
      </vs-select>
    </div>
  </div>
</template>

<script>

import {mapGetters, mapMutations} from 'vuex';

export default {
  data() {
    return {
      search: '',
      view: '',
    }
  },
  computed: {
    ...mapGetters(['burgerActiveForNav'])
  },
  methods: {
    ...mapMutations(['viewChange', 'getBySearch', 'burgerMenuActive']),
    changeView() {
      this.viewChange(this.view)
    },
    burgerActive() {
      this.burgerMenuActive()
      console.log(this.burgerActiveForNav)
    },
    getBySearchQuery() {
      this.getBySearch(this.search)
    }
  }
}
</script>

<style scoped>

.burger_menu {
  display: none;
}

.navbar-filters {
  display: flex;
  align-items: center;
  gap: 30px;
}

.navbar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: white;
  padding: 20px 50px 20px 270px;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  transition: .5s;

}
</style>